import HealthInfo from ".";

const HealthInfoArc = ({ data, index, createArc, colors, format }) =>
  <g key={index} className="arc">
    <path className="arc" d={createArc(data)} fill={colors(index)} />
    <text
      transform={`translate(${createArc.centroid(data)})`}
      textAnchor="middle"
      alignmentBaseline="middle"
      fill="white"
      fontSize="10"
    >
      {data.data.label}
      {`: `}
      {format(data.value)}
    </text>
  </g>;

export default HealthInfoArc;
